export default {
    props: {
        badge: {
            type: Number,
            default: 0
        },
        type: {
            type: String,
            default: 'common'
        }
    },
    data() {
        return {
            tapping: false
        }
    },
    computed: {
        tappingClass() {
            return (this.tapping ? 'tapping ' : '') + this.type
        }
    },
    methods: {
        clickBuntton() {
            this.$emit('click',{
                type:this.type,
                badge:this.badge
            })
        },
        clickStart() {
            this.tapping = true;
        },
        cancelTap() {
            this.tapping = false;
        }
    },
}
